import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

// 简单的应用
// state (全局变量的数据)
// getters (用来获取数据的方法)
// actions (跟后台接口打交道的方法)
// mutations (存储数据的方法)

// vueComponent(组件) -> actions
// -> mutations -> state -> vueComponent(组件)
// vueComponent(组件) -> getters -> state

/**
 * you can add your module here
 */
let store = new Vuex.Store({
  // 1. state
  state: {
    count: 10,
    userInfo: {
      'username': '张三',
      'age': '22',
      'phone': '15711112222',
      'address': '上海市青浦区汇联路33号'
    },
    todoList: [
      { id: 1, text: '...', done: true },
      { id: 2, text: '...', done: false },
      { id: 3, text: '...', done: false },
      { id: 4, text: '...', done: true },
      { id: 5, text: '...', done: false }
    ]
  },
  // 2. getters
  getters: {
    // 参数列表state指的是state数据
    getUserInfo (state) {
      return state.userInfo
    },
    doneTodos: state => {
      return state.todoList.filter(todoList => todoList.done)
    },
    doneTodosCount: (state, getters) => {
      return getters.doneTodos.length
    }
  },
  mutations: {
    // state指的是state的数据  value传递过来的数据
    userInfo (state, value) {
      // 将传参设置给state的userInfo
      state.userInfo = value
    },
    setMyInfo (state, myInfo) {
      state.myInfo = myInfo
    },
    increment (state, value) {
      state.count += value
    }
  },
  modules: {
  }
})

store.registerModule('vux', {
  state: {
    demoScrollTop: 0
  },
  mutations: {
    updateDemoPosition (state, payload) {
      state.demoScrollTop = payload.top
    }
  },
  actions: {
    updateDemoPosition ({commit}, top) {
      commit({type: 'updateDemoPosition', top: top})
    }
  }
})

export default store



