import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

// const HelloWorld = r => require.ensure([], () => r(require('@/components/HelloWorld.vue')), 'HelloWorld')

const Tab = r => require.ensure([], () => r(require('@/views/Tab.vue')), 'Tab')
const Home = r => require.ensure([], () => r(require('@/views/Home/Home.vue')), 'Home')
const Store = r => require.ensure([], () => r(require('@/views/StoreTest/Index.vue')), 'Store')

// 路由跳转
const A = r => require.ensure([], () => r(require('@/views/RouterTest/A.vue')), 'A')
const B = r => require.ensure([], () => r(require('@/views/RouterTest/B.vue')), 'B')
const Provinces = r => require.ensure([], () => r(require('@/views/Address/Provinces.vue')), 'Provinces')
const Citys = r => require.ensure([], () => r(require('@/views/Address/Citys.vue')), 'Citys')

export default new Router({
  routes: [
    { path: '/', name: 'Tab', component: Tab },
    { path: '/home', name: 'Home', component: Home },
    { path: '/store', name: 'Store', component: Store },
    { path: '/a', name: 'A', component: A },
    { path: '/b', name: 'B', component: B },
    { path: '/provinces', name: 'Provinces', component: Provinces },
    { path: '/citys', name: 'Citys', component: Citys }
    // { path: '/helloworld', name: 'HelloWorld', component: HelloWorld }
  ]
})



